<template>
    <div class="login">
        <div class="container">
            <div class="login-container" style="margin-top: 40px;">
                <div class="text-center">
                    <img src="static/images/logo-3.png" alt="" style="width: 300px;">
                </div>
                <div class="text-center" style="margin: 20px 0;font-size: 26px;">
                    
                </div>

                <div style="width: 360px;margin: 0 auto;">
                    <v-form direction="horizontal" :model="ruleForm" :rules="rules" ref="ruleForm">
                        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol" prop="phoneNumber">
                            <v-input placeholder="" v-model="ruleForm.phoneNumber">
                                <v-icon type="123" slot="before">
                                    <i class="iconfont icon-shoujihao" style="font-size: 22px;"></i>
                                </v-icon>
                            </v-input>
                        </v-form-item>

                        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol" prop="qcode">
                            <div class="inline-block text-center" style="width: 55%;">
                                <v-input placeholder="" v-model="ruleForm.qcode">
                                    <v-icon type="123" slot="before">
                                        <i class="iconfont icon-unie615" style="font-size: 22px;"></i>
                                    </v-icon>
                                </v-input>
                            </div>
                            <div class="inline-block text-center" style="width: 43%;">
                                <a href="javascript:;" @click="getQcode">{{qcodeText}}</a>
                            </div>
                        </v-form-item>

                        <v-form-item :label-col="labelCol" :wrapper-col="wrapperCol">
                            <v-button type="primary" html-type="submit"
                                      @click.prevent="submitForm('ruleForm')"
                                      style="display: block;width: 100%;font-size: 22px;"></v-button>
                        </v-form-item>

                        <v-form-item>
                            <v-checkbox v-model="ruleForm.isTy"><a href="javascript:;" @click.prevent="visible = true"></a></v-checkbox>
                        </v-form-item>

                        <v-form-item>
                            <div class="text-center">
                                <a href="javascript:;">
                                    <i class="iconfont icon-return"></i>
                                    <span style="margin-left: 5px;"></span>
                                </a>
                            </div>
                        </v-form-item>
                    </v-form>
                </div>


                <v-modal title=""
                         :visible="visible"
                         :width="800"
                         ok-text=""
                         cancel-text=""
                         @ok="handleOk"
                         @cancel="handleCancel">
                    <div style="height: 430px;overflow: auto;">1<br/>
                        <br/>
                        2<br/>
                        <br/>
                        1<br/>
                        2,<br/>
                        1)<br/>
                        2)<br/>
                        3<br/>
                        <br/>
                        4<br/>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                        6<br/>
                        <br/>


                        7/<br/>
                        <br/>
                        (1)<br/>
                        (2)<br/>
                        <br/>
                        8<br/>
                        <br/>
                        <br/>
                        <br/>
                        9<br/>
                        /<br/>
                        10<br/>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                    </div>

                </v-modal>
            </div>

            <div class="text-center">
                XXX  ICP17033346
            </div>
        </div>
    </div>
</template>

<script>
    var isMobileNum = function(num) {
        return /^(1[3-8]\d{9})$/.test(num);
    }

    var isLoginQcode = function (n) {
        return /^\d{4}$/.test(n);
    };

    var validatePhoneNumber = function(rule, value, callback) {
        if(isMobileNum(value)){
            callback();
        }else{
            callback(new Error(''));
        }
    };

    var validateQcode = function(rule, value, callback) {
        if(isLoginQcode(value)){
            callback();
        }else{
            callback(new Error(''));
        }
    };

    export default {
        name: 'Login',
        data () {
            return {
                labelCol: {
                    span: 6
                },
                wrapperCol: {
                    span: 24
                },
                ruleForm: {
                    phoneNumber: '',
                    qcode: '',
                    isTy: false

                },
                rules: {
                    phoneNumber: [
                        {
                            required: true,
                            message: ''
                        },
                        {
                            validator: validatePhoneNumber
                        }
                    ],
                    qcode: [
                        {
                            required: true,
                            message: ''
                        },
                        {
                            validator: validateQcode
                        }
                    ]
                },
                visible: false,

                qcodeText: "",
                //60
                isSend: false
            }
        },
        methods: {
            //
            handleOk: function () {
                this.visible = false;
                this.ruleForm.isTy = true;
            },

            //
            handleCancel: function () {
                this.visible = false;
            },

            //
            getQcode: function () {
                var vm = this;
                console.log(vm.isSend)
                if(!vm.isSend){
                    vm.isSend = true;

                    //
                    this.$refs.ruleForm.validateField("phoneNumber", function (a) {
                        if(a === ""){
                            vm.$czapi.getPhoneCaptcha({
                                phone: vm.ruleForm.phoneNumber
                            }).then(function (data) {
                                vm.isSend = true;
                                var count = data.interval, timer = null;
                                vm.qcodeText = count + "";
                                timer = setInterval(function () {
                                    count--;
                                    if(count > 0){
                                        vm.qcodeText = count + "";
                                    }else{
                                        vm.isSend = false;
                                        vm.qcodeText = "";
                                        clearInterval(timer);
                                    }
                                }, 1000);
                            }).fail(function () {
                                vm.isSend = false;
                            });
                        }else{
                            vm.isSend = false;
                        }

                    });
                }
            },

            submitForm: function(formName) {
                var vm = this;
                vm.$refs[formName].validate(function(valid) {
                    if (valid) {
                        //alert('!');
                        vm.$czapi.doLogin({
                            phone: vm.ruleForm.phoneNumber,
                            captcha: vm.ruleForm.qcode,
                        }).then(function (data) {
                            var expiresDate = new Date(data.expired);
                            //cookietoken
                            $.cookie("userToken", data.token, {
                                    path : '/',//cookie
                                    expires : expiresDate
                                }
                            );

                            //
                            vm.$router.push({ name: 'Index'})

                            // 
                            //vm.$router.push('/home/first')

                            // 
                            //vm.$router.push({ path: '/home/first' })

                            // 
                            //vm.$router.push({ name: 'home', params: { userId: wise }})
                        });
                    } else {
                        return false;
                    }
                });
            }
        },
        mounted(){
            //console.log(this.$czapi);
        }
    }
</script>

